  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Title {
   font-family: 'Lexend Peta', sans-serif;
   font-size: 2rem;
   font-weight: bold;
   padding-top: 25px;
   text-align: center;

}

.TitleH {
   font-family: 'Lexend Peta', sans-serif;
   font-size: 2rem;
   font-weight: bold;
   padding-top: 25px;
   text-align: center;
   

}
.arrow,
.arrow:before
{
  position: absolute;
  left: 50%;
  bottom: 15%;
}


@-webkit-keyframes arrow
{
  0% {
    opacity: 1;}
  100% {
    opacity: 0;
    transform: translate(-10px, -10px);}
}


@keyframes arrow
{
  0% {
    opacity: 1;}
  100% {
    opacity: 0;
    transform: translate(-10px, -10px);}
}
  

 @keyframes colorchange
 {
   0%   { color: cyan;}
   33%  { color: magenta;}
   66%  { color: yellow;}
   100% { color: cyan;}
}

 @-webkit-keyframes colorchange /* Safari and Chrome - necessary duplicate */
 {
   0%   { color: cyan;}
   33%  { color: magenta;}
   66%  { color: yellow;}
   100% {color: cyan;}
 }
#home{
   position: relative;
   height: 100%;
  
   
   text-align: center;
   overflow: hidden;
 }
 /* vertically center banner section */
 header:before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%;
 }
 header .banner {
    display: inline-block;
    
    margin: 100px auto;
    width: 100%;
    text-align: center;
 }
 
 header .banner-text { width: 100%; }
 header .banner-text h1 {
    font: 3rem 'Raleway', sans-serif;
    font-weight: bold;
    letter-spacing: -2px;
    margin: 0 auto 18px auto;
    animation: colorchange 60s linear 1s infinite; /* animation-name followed by duration in seconds*/
    /* you could also use milliseconds (ms) or something like 2.5s */
 -webkit-animation: colorchange 60s linear 0s infinite alternate; /* Chrome and Safari */
 }
 
 /* header social links */
 .social {
    margin: 24px 0;
    padding: 0;
    font-size: 30px;
    text-shadow: 0px 1px 2px rgba(0, 0, 0, .8);
   
       
    
 }

 @media only screen and (min-width: 600px){
   .social li a { color:white; border-radius: 100%;}
   header .banner-text h3 {
      font: 18px/1.9em 'Lato', serif;
      color: white;
      margin: 0 auto;
      width: 70%;
   }
   header .banner-text h3 span,
   header .banner-text h3 a {
      color: #fff;
   }
   header .banner-text hr {
      width: 50%;
      margin: 18px auto 24px auto;
      color: rgb(34, 33, 34);
   }
   
   .TitleH{
      color: white !important;
   }
   .arrow
{
  width: 40px;
  height: 40px;
  
  margin: -20px 0 0 -20px;
  -webkit-transform: rotate(45deg);
  border-left: none;
  border-top: none;
  border-right: 2px #fff solid  !important;
  border-bottom: 2px #fff solid  !important;
  bottom: 15%;
  padding: 0 !important;
}
.arrow:before
{
  content: '';
  width: 20px;
  height: 20px;
  top: 50%;
  margin: -10px 0 0 -10px;
  border-left: none;
  border-top: none;
  border-right: 1px #fff solid !important;
  border-bottom: 1px #fff solid !important;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: arrow;
          animation-name: arrow;
}
  
 }
