body.light-mode{background-color:#fff;color:#333;transition:background-color 0.3s ease}body.light-mode .fp-controlArrow.fp-prev{border-color:transparent black transparent transparent !important}body.light-mode .fp-controlArrow.fp-next{border-color:transparent  transparent transparent black !important}body.light-mode .ic{color:black !important}body.dark-mode{background-color:black;color:#dfdfdf}body.dark-mode .ic{color:white !important}body.dark-mode .social li a{color:white;border-radius:100%}body.dark-mode .arrow:before{border-right:1px #fff solid !important;border-bottom:1px #fff solid !important}body.dark-mode .arrow{border-right:1px white solid;border-bottom:1px white solid}body.dark-mode h3{color:white}body.dark-mode .bars li em{color:white}body.dark-mode fp-controlArrow.fp-prev{border-color:transparent white transparent transparent !important}body.dark-mode fp-controlArrow.fp-next{border-color:transparent white transparent transparent !important}body.dark-mode .dark-mode-toggle>button{color:#999}body.dark-mode .dark-mode-toggle>button:last-child{color:lightblue}.dark-mode-toggle{display:flex;position:absolute;right:2%;top:5%;z-index:200}.dark-mode-toggle>button{font-size:1.2em;background:none;border:none;color:#ffe600;cursor:pointer;transition:color 0.3s ease}.dark-mode-toggle>button:last-child{color:#666}.dark-mode-toggle>button:focus{outline:none}.toggle-control{position:relative;padding:0 4px;display:flex;align-items:center}input[type='checkbox'].dmcheck{width:40px;height:10px;background:#555;position:relative;border-radius:5px;-webkit-appearance:none;-moz-appearance:none;appearance:none;cursor:pointer;vertical-align:2px;outline:none}input[type='checkbox'].dmcheck:checked+label{left:30px}input[type='checkbox'].dmcheck:focus-visible{outline:solid 2px white}input[type='checkbox'].dmcheck+label{display:inline-block;width:18px;height:18px;border-radius:50%;transition:all 0.3s ease;cursor:pointer;position:absolute;left:2px;background:#fff;opacity:0.9;background-color:#f6f6f6}

