header {
  position: relative;
  word-spacing: 5px;
  min-height: 48px;
   width: auto;
  text-align: center;
  overflow: hidden;
}

header a {
  margin: 5px;
  display: inline-block;
   
   line-height: 32px;
	text-decoration: none;
   text-align: left;
   color: #fff;
	transition: color .2s ease-in-out;
}
ul#nav {
  min-height: 48px;
  width: auto;

  /* center align the menu */
  text-align: center;
}
ul#nav li {
  position: relative;
  list-style: none;
  height: 48px;
  display: inline-block;
}

/* Links */
ul#nav li a {

/* 8px padding top + 8px padding bottom + 32px line-height = 48px */

  display: inline-block;
  padding: 8px 13px;
  line-height: 32px;
   
  text-align: left;
   transition: color .2s ease-in-out;
}

ul#nav li a:active { background-color: transparent !important; }
ul#nav li.current a { color: #F06000; }
