
 .contact {
    background-color: #191919;
    
    color: #636363;
    
    background-size: cover !important;
    
    flex-direction: row;
    justify-content: space-between;
    display: flex;
    
    margin-top: 5vh;
    
 }

 .tag{
     color: blanchedalmond;
     font: 1.8rem 'Raleway', serif;
     width: 100%;
     text-align: center;
     margin-top: 5%;
     animation: colorchange1 15s linear 1s infinite; /* animation-name followed by duration in seconds*/
     /* you could also use milliseconds (ms) or something like 2.5s */
  -webkit-animation: colorchange1 15s linear 0s infinite alternate; /* Chrome and Safari */
 }

 .con{
    background-color: #191919;
    color: #636363;
    background-size: cover !important;
    height: 70vh;
    margin-top: 5vh;
 }
 
 h3{
 font: 1.8rem 'Lato', serif;
color: white;
width: 100%;
    text-align: center;
 }

 .add {
     text-align: center;
     width: 45%;
     color: white;
     font: 1.2rem 'Raleway', serif;
 }

 .conn {
    
    width: 45%;
    font: 18px/1.9em 'Lato', serif;
}

@keyframes colorchange1
 {
   0%   { color: rgb(158, 209, 243)}
   33%  { color: rgb(16, 233, 248)}
   66%  { color: rgb(168, 219, 235)}
   100% { color: rgb(158, 209, 243)}
}

 @-webkit-keyframes colorchange1 /* Safari and Chrome - necessary duplicate */
 {
    0%   { color: rgb(158, 209, 243)}
    33%  { color: rgb(16, 233, 248)}
    66%  { color: rgb(168, 219, 235)}
    100% { color: rgb(158, 209, 243)}
 }
.sites {
    display: flex;
    color: white;
    flex-direction: row;
    justify-content: space-around;
    
    font: 18px/1.9em 'Lato', serif !important;
}

.sites a{
    margin: 15px;
    color: white;
    
   
}

.s{
    font: 18px/1.9em 'Raleway', serif !important; 
    margin-left: 5px;
}

.sites a:hover{
    color: gray;
}
@media only screen and (max-width: 600px){

    .contact{
        display: flex;
        flex-direction: column;
        font-size: 1rem;
        
    }
    .tag{
    display: none;
    }

    .add{
        width: 100% !important;
    }
    .conn{
        width: 100% !important;
    }

    .addre{
        font-size: 1rem;
        line-height: 1.2rem;
        margin-top: 5px;
    }
    .sites{
        
        justify-content: space-between;
    }
    h3{
        margin-bottom: 0px;
    }
    
}

.footer {
    font-family: 'Raleway', sans-serif;
   font-size: 1rem;
   font-weight: bold;
   padding-top: 5px;
   text-align: center;
   color: gray;
}