  .dark-mode-toggle {
    display: flex;
    
    position: absolute;
    right: 2%;
    top: 5%;
    z-index: 200;

    & > button {
      font-size: 1.2em;
      background: none;
      border: none;
      color: #ffe600;
      cursor: pointer;
      transition: color 0.3s ease;
      &:last-child {
        color: #666;
      }
  
      &:focus {
        outline: none;
      }
    }
  }
  
  .toggle-control {
    position: relative;
    padding: 0 4px;
    display: flex;
    align-items: center;
  }
  
  input[type='checkbox'].dmcheck {
    width: 40px;
    height: 10px;
    background: #555;
    position: relative;
    border-radius: 5px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
    vertical-align: 2px;
    outline: none;
  
    &:checked + label {
      left: 30px;
    }
  
    &:focus-visible {
      outline: solid 2px white;
    }
  
    & + label {
      display: inline-block;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      transition: all 0.3s ease;
      cursor: pointer;
      position: absolute;
      left: 2px;
      background: #fff;
      opacity: 0.9;
      background-color: #f6f6f6;
    }
  }
  