@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400&family=Lexend+Peta&family=Raleway:wght@100;400;700&display=swap');

nav {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    
    
    width: 300px;
  }
  
  .background {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 300px;
    background: rgb(214, 214, 214);
    z-index: 200;
  }
  .l{
  
    position: absolute;
    
  }
  .buttonSide {
    outline: none;
    border: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    cursor: pointer;
    position: absolute;
    top: 18px;
    left: 15px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: transparent;
    z-index: 200;
  }
  
  .unor,
  .list {
    margin: 0;
    padding: 0;
    z-index: 200;
  }
  
  .unor {
    padding: 25px;
    position: absolute;
    top: 100px;
    width: 230px;
    

  }
  
  .list {
    list-style: none;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  
  .text-place {
    border-radius: 5px;
    width: 200px;
    height: 20px;
    flex: 1;
    text-align: center;
    padding-top: 2px;
    padding-bottom: 2px;
    font-family: 'Raleway', sans-serif;
    font-size: 1rem;
  }


  @media only screen and (max-width: 600px){
      nav, .background{
        width: 100%;
        
      }

      .unor{
        margin: auto;
        margin-left: 15%;
      }

  }