@media only screen and (max-width: 600px){
    .LeftSection {
        display: none;
        
     }

     .RightSection{
         width: 80%;
         
     }
     .Title1 {
        font-family: 'Lexend Peta', sans-serif;
        font-size: 2rem;
        font-weight: bold;
        
        text-align: center;
        margin-left: 25%;
     
     }
     .Education{
   
        height: 70vh;
        font-family: 'Raleway', sans-serif;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        
        margin-bottom: auto;
     }
     
     
}
@media only screen and (min-width: 600px){
    .LeftSection {
        width: 45%;
        
     }

     .RightSection {
   
        width: 45%;
        font: 'Lato', serif;
     }
     .Title1 {
        font-family: 'Lexend Peta', sans-serif;
