.git {
    -moz-animation: git 2s ease;
    -webkit-animation: git 2s ease;
}
.illustrator {
    -moz-animation: illustrator 2s ease;
    -webkit-animation: illustrator 2s ease;
}
.reactjs {
    -moz-animation: reactjs 2s ease;
    -webkit-animation: reactjs 2s ease;
}
.css {
    -moz-animation: css 2s ease;
    -webkit-animation: css 2s ease;
}
.html5 {
    -moz-animation: html5 2s ease;
    -webkit-animation: html5 2s ease;
}
.vuejs {
    -moz-animation: vuejs 2s ease;
    -webkit-animation: vuejs 2s ease;
}
.mongodb {
    -moz-animation: java 2s ease;
    -webkit-animation: java 2s ease;
}

@-moz-keyframes git {
0%   { height: 0px;  }
100% { height: 60%;  }
}
@-moz-keyframes illustrator {
0%   { height: 0px;  }
100% { height: 55%;  }
}
@-moz-keyframes reactjs {
0%   { height: 0px;  }
100% { height: 50%;  }
}
@-moz-keyframes css {
0%   { height: 0px;  }
100% { height: 90%;  }
}
@-moz-keyframes html5 {
0%   { height: 0px;  }
100% { height: 80%;  }
}
@-moz-keyframes vuejs {
0%   { height: 0px;  }
100% { height: 50%;  }
}
@-moz-keyframes java {
0%   { height: 0px;  }
100% { height: 80%;  }
}

