@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400&family=Lexend+Peta&family=Raleway:wght@100;400;700&display=swap');


.About{
   
   background-size: cover !important;
   height: 70vh;
   
   display: flex;
   flex-direction: column;
   margin-top: 15vh;
}

.DP1 {
   
   width: 100%;
   align-items: center;
   vertical-align: middle;
   height: 50%;
   display: inline-block;
  
}
.DP2 {
   
   width: 100%;
   align-items: center;
   vertical-align: middle;
   height: 50%;
   display: inline-block;
  
}
.DP3 {
   
   width: 100%;
   align-items: center;
   vertical-align: middle;
   height: 50%;
   display: inline-block;
  
}

@media only screen and (max-width: 600px){
   .DP1{
      display: none;
   }
   .DP3{
      display: none;
   }
   .About{
      display: inline;
   }
   .pic{
      display: inline;
   }
   .profile-pic {
      position: relative;
      display: block;
      width: 275px;
      height: 300px;
      border-radius: 100%;
      
      align-self: center;
      margin:auto;
      margin-top: 5%;
   }
   .address {
      font-family:'Raleway', sans-serif;
      
      
   }
   
   .Content{
      font-size: 1rem;
      width: 100%;
      text-align: center;
   }
}
@media only screen and (min-width: 600px){
   .Content {
      width: 100%;
      
      text-align: center;
      font-family: 'Lato', sans-serif;
      font-size: 1.5rem;
      }
      .profile-pic {
         position: relative;
         display: block;
         width: 250px;
         height: 250px;
         border-radius: 100%;
         
         align-self: center;
         margin:auto;
      }
      .address {
         font-family: 'Raleway', sans-serif;
         
         
         font-size: 16pt;
         line-height: 18pt
      }
      
}

.pic {
   display: flex;
   justify-content: space-around;
}


.Hobby {
   width: 100%;
   margin: auto;
   text-align: center;
   font-family: 'Raleway', sans-serif;
   
}


.btn {
   background-color: black;
   border-radius: 10px;
   width: 120px;
   height: 50px;
   color: white;
   font-family: 'Lato', sans-serif;
   font-size: 1rem;
}

